.text-page {
    --h1-color: #fff;
    --other-text-color: #{rgba($color: #fff, $alpha: .65)};

    //    h1
    --h1-font-size: #{clampFluid(30, 60, 320, 1920)};
    //    h2
    --h2-font-size: #{clampFluid(40)};
    //    h3
    --h3-font-size: #{clampFluid(32)};
    //    h4
    --h4-font-size: #{clampFluid(24)};
    //   p  li
    --text-font-size: #{clampFluid(14, 20, 320, 1920)};
    //     
    --caption-font-size: #{clampFluid(14)};
    --gallery-item-aspect-ratio: 4/3;

    //   h1
    --title-margin-bottom: #{clampFluid(40)};
    //   h2,h3,h4
    --heading-margin-bottom: #{clampFluid(20)};
    //   
    --heading-margin-top: #{clampFluid(50)};
    //  p
    --text-margin-bottom: #{clampFluid(20)};
    //  li
    --li-margin-bottom: #{clampFluid(10)};
    //    
    --content-block-margin-bottom: #{clampFluid(80)};
    //    
    --gallery-grid-gap: #{clampFluid(15)};

    //   
    --img-border-radius: 0;

    //  
    --visited-link-color: #653465;
    //    
    --title-underline-border-color: currentColor;
    --star-icon: url(#{$white-star});
    

    ul{
        padding-left: 0;
        li{
            list-style: none;
            &::before{
                content: "";
                width: clampFluid(23);
                height: clampFluid(23);
                display: inline-block;
                vertical-align: middle;
                margin-right: clampFluid(10);
                background: transparent center/cover no-repeat;
                background-image: var(--star-icon);
            }
        }
    }
}